<template>
  <div style="padding: 10px">
    <div style="margin: 10px 0 ;display: flex">
      <el-button type="primary" @click="handleEdit3">新增标签</el-button>
<!--      <el-button type="primary">导入</el-button>-->
<!--      <el-button type="primary">导出</el-button>-->
    </div>
<!--    <div style="margin: 10px 0 ;display: flex">-->
<!--      <el-input v-model="search" placeholder="请输入RFID" style="width: 20%" clearable/>-->
<!--      <el-button type="primary" style="margin-left: 5px">查询</el-button>-->
<!--    </div>-->
    <el-table :data="tableData" stripe style="width: 100%" border>
      <el-table-column prop="id" label="ID" width="150px" sortable/>
      <el-table-column prop="rfid" label="rfid" width="450px" />
      <el-table-column prop="productAbbreviationCode" label="产品简码号" width="450px" />
<!--      <el-table-column prop="manufacturername" label="厂商名称" width="150" />-->
<!--      <el-table-column prop="manufacturerphone" label="厂商电话" width="150" />-->
<!--      <el-table-column prop="latitude" label="最终纬度" width="150" />-->
<!--      <el-table-column prop="longitude" label="最终经度" width="150" />-->
      <el-table-column fixed="right" label="操作" width="170px">
        <template #default="scope">
          <el-button type="text" size="small" @click="handleEdit(scope.row)"
          >编辑</el-button
          >
<!--          <el-button type="text" size="small" @click="handleEdit2(scope.row)"-->
<!--          >溯源</el-button-->
<!--          >-->
          <el-popconfirm title="确定删除吗?" @confirm="handleDelete(scope.row.id)">
            <template #reference>
              <el-button type="text" size="small">删除</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin: 10px 0">
      <el-pagination
          v-model:currentPage="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[5, 10, 15, 20]"
          :small="0"
          :disabled="false"
          :background="true"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
    <el-dialog
        v-model="dialogVisible"
        title="编辑标签"
        width="30%"
        @close="closeAddDialog"
    >
      <el-form ref="addRoleForm" :model="form" label-width="120px">
                <el-form-item label="rfid" style="width: 80%">
                  <el-input v-model="form.rfid" />
                </el-form-item>
                <el-form-item label="产品简码号" style="width: 80%">
                  <el-input v-model="form.productAbbreviationCode" />
                </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="closeAddDialog">取消</el-button>
        <el-button type="primary" @click="save"
        >确定</el-button
        >
      </span>
      </template>
    </el-dialog>
    <el-dialog
        v-model="dialogVisible2"
        title="溯源"
        width="50%"
    >
      <el-form :model="form" label-width="120px">
        <el-form-item label="厂商名称" style="width: 80%">
          <el-input v-model="form.manufacturername" />
        </el-form-item>
        <el-form-item label="厂商电话" style="width: 80%">
          <el-input v-model="form.manufacturerphone" />
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
        v-model="dialogVisible3"
        title="新增芯片"
        width="30%"
    ><el-form :model="form" label-width="120px">
      <el-form-item label="rfid" style="width: 80%">
        <el-input v-model="form.rfid" />
      </el-form-item>
      <el-form-item label="产品简码号" style="width: 80%">
        <el-input v-model="form.productAbbreviationCode" />
      </el-form-item>
    </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible3 = false">取消</el-button>
        <el-button type="primary" @click="save"
        >确定</el-button
        >
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import request from "@/utils/request";
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Chip",
  data(){
    return{
      form:{},
      dialogVisible:false,
      dialogVisible2:false,
      dialogVisible3:false,
      currentPage:1,
      pageSize:10,
      total:10,
      search:'',
      tableData:[]
    }
  },
  created() {
    this.load()
  },
  methods:{
    load(){
        request.post("/chip/queryByLimit?pageNum="+this.currentPage+"&pageSize="+this.pageSize+"&search="+this.search
        // pageNum:this.currentPage,
        // pageSize:this.pageSize,
        // search:this.search
      ).then(res=>{
        this.tableData=res.data.records
        this.total=res.data.total
      })
    },
    add(){
      this.dialogVisible=true,
          this.form={}
    },
    //清除对话框中的数据
    closeAddDialog() {
      this.dialogVisible = false   //关闭对话框
      // this.$refs.addRoleForm.resetFields();
      this.form = this.$options.data().form
    },


    save(){
      if(this.form.id){
        request.put("/chip",this.form).then(res => {
          console.log(res)
          if(res.code==="0"){
            this.$message({
              type:"success",
              message:"更新成功"
            })
          }else {
            this.$message({
              type:"error",
              message:res.msg
            })
          }
          this.load()
          this.dialogVisible=false
        })
      }else{
        request.post("/chip",this.form).then(res => {
          console.log(res)
          if(res.code==="0"){
            this.$message({
              type:"success",
              message:"新增成功"
            })
          }else {
            this.$message({
              type:"error",
              message:res.msg
            })
          }
          this.load()
          this.form = this.$options.data().form
          this.dialogVisible3=false
        })
      }
    },

    // eslint-disable-next-line no-unused-vars
    handleEdit(row){
      this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible = true
    },
    handleEdit2(row){
      this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible2 = true
    },
    handleEdit3(){
      // this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible3 = true
    },
    handleDelete(id){
      request.delete("/chip/"+id).then(res=>{
        if(res.code==="0"){
          this.$message({
            type:"success",
            message:"删除成功"
          })
        }else {
          this.$message({
            type:"error",
            message:res.msg
          })
        }
        this.load()
      })
    },
    handleSizeChange(pageSize){
      this.pageSize=pageSize
      this.load()
    },
    handleCurrentChange(pageNum){
      this.currentPage=pageNum
      this.load()
    }
  }
}

</script>

